.projects-main{
    width: 99vw;
    height: 100vh;
}

.project-text{
    display: flex;
    justify-content: center;
    font-size: 3vh;

    
}
.projects-scroller{
    width: 30vw;
    height: 80vh;
    overflow-y: hidden;

    background-color: #f5f5f5;
    border-radius: 15px;
    transition: 0.5s;
}

.projects-scroller:hover{
    box-shadow: 0.5em 0.5em 0px #219EBC;
}

.scroller{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100vw;
    height: 90vh;
}

.img-project{ 
    background-color: #f5f5f5;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
    padding: 5px;
}

.img-project:hover{
    cursor: pointer;
}


.img-project img{
    width: 100%;
    border-radius: 15px;
}

.text{
    margin: 5px;
    font-size: 2.5vh;
    background-color: #f5f5f5;

}

.proj-title{
    color:#132561 ;
    background-color: #f5f5f5;
}

img{
    width: 20vw;
    background-color: #f5f5f5;
    /* margin: 5px; */
}